.App {
  text-align: center;
  min-height: 100vh;
  background-color: #E6F5F8;

}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.sticky + .App-body {
  margin-top: 2.1875rem;
}

.aboutName {
  color: #0077b6;
}

.aboutWorkLink {
  display: flex;
  justify-content: center;
  align-items: baseline;
  gap: 15px;
}

.aboutWorkLink:hover {
  color: #0077b6;
  cursor: pointer;
}

.App-link {
  background-color: #61dafb;
}
/* add Media Query to make 1 column on phone displays */

.App-AboutBody{
  height: 100vh;
  margin: 0 ;
  color: white;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  justify-content: center;

  font-size: calc(10px + 2vmin);
}

h2 {
  margin: 0;
  z-index: 10;
}

.cardHolder {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: minmax(250px, auto);

  gap: 10px;
  padding: 10px 40px;
}

.App-body{
  height: 100vh;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%

}

.circle1 {
  position: absolute;
  background-color: #494e58;

  border-radius: 50%;

  left: -50px;
  top: -50px;

	animation-name: innerCircleAnimation;
	animation-duration: 10s;
	animation-direction: alternate;
	animation-iteration-count: infinite;

z-index: 0;


}

.circle2 {
  position: absolute;
  background-color: #1e325c;

  border-radius: 50%;


	animation-name: circle2;
	animation-duration: 10s;
	animation-direction: alternate;
	animation-iteration-count: infinite;

z-index: 1;


}

.circle3 {
  position: absolute;
  background-color: #2e4c8d;

  border-radius: 50%;

  left: -50px;
  top: -50px;

	animation-name: circle3;
	animation-duration: 10s;
	animation-direction: alternate;
	animation-iteration-count: infinite;

z-index: 2;


}

.circle4 {
  position: absolute;
  background-color: #6d84b1;

  border-radius: 50%;

  left: -50px;
  top: -50px;

	animation-name: circle4;
	animation-duration: 10s;
	animation-direction: alternate;
	animation-iteration-count: infinite;

z-index: 3;


}

@keyframes innerCircleAnimation {
	0% {
		width: 0rem;
		height: 0rem;
    left: 0rem;
    top: 0rem;
    opacity: 0;
	}

	100% {
		width: 90rem;
		height: 90rem;
    left: -45rem;
    top: -45rem;
    opacity: 1;
	}
}

@keyframes circle2 {
	10% {
		width: 0rem;
		height: 0rem;
    left: 0rem;
    top: 0rem;
    opacity: 0;
	}

	100% {
		width: 80rem;
		height: 80rem;
    left: -40rem;
    top: -40rem;
    opacity: 1;
	}
}

@keyframes circle3 {
	20% {
		width: 0rem;
		height: 0rem;
    left: 0rem;
    top: 0rem;
    opacity: 0;
	}

	100% {
		width: 70rem;
		height: 70rem;
    left: -35rem;
    top: -35rem;
    opacity: 1;
	}
}

@keyframes circle4 {
	30% {
		width: 0rem;
		height: 0rem;
    left: 0rem;
    top: 0rem;
    opacity: 0;
	}

	100% {
		width: 60rem;
		height: 60rem;
    left: -30rem;
    top: -30rem;
    opacity: 1;
	}
}



@media screen and (min-width: 760px){

  .cardHolder {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(250px, auto);

    gap: 30px;
    padding: 10px 40px;

  }
}